@keyframes scroll {
  0% {
    transform: translateX(0%);
  }
  100% {
    transform: translateX(-50%);
  }
}

@keyframes upToDown {
  0% {
    transform: translateY(10px);
  }
  100% {
    transform: translateY(50px);
  }
}

@keyframes changeFilter {
  0% {
    filter: grayscale(100%);
  }
  100% {
    filter: grayscale(0);

  }
}