@use 'additional/mixins' as *;

.support {
    max-width: 100vw;
    width: 100%;
    &__title {
        margin-bottom: 5.625rem;
    }

    &-wrapper {
        max-width: 100vw;
        width: 100%;
    }

    &__btns {
        display: flex;
        align-items: center;
        width: max-content;
    }

    &__slide {
        max-width: 20rem;
        
        &.swiper-slide-active {
            .support__img {
                filter: none;
            }
        }

        @media (hover: hover) {
            &:hover {
                .support__img {
                    filter: none;
                }
            }
        }
    }
    &__img {
        margin-bottom: 2.1875rem;
        aspect-ratio: 0.8 / 1;
        filter: grayscale(100%);
        object-fit: cover;
        transition: 0.1s;
        max-width: 20rem;
        width: 100%;
    }

    &__text {
        text-transform: uppercase;
        font-size: clamp(1.25rem, 1.5rem + 0.3vw, 1.875rem);
        font-weight: 600;
        line-height: 2;
    }

    &__btn {
        position: static;
        display: flex;
        justify-content: center;
        align-items: center;
        width: 7.5rem;
        height: 7.5rem;
        cursor: pointer;
        transition: 0.2s;

        &[disabled] {
            opacity: 0.5;

            &:hover {
                background-color: transparent;

                svg {
                    stroke: var(--clr-white);
                }
            }
        }

        svg {
            fill: transparent;
            stroke: var(--clr-white);
            width: 40px;
            height: 40px;
            transition: 0.2s;
        }

        @media (hover: hover) {
            &:hover {
                background-color: var(--clr-white);

                svg {
                    stroke: var(--clr-black);
                }
            }
        }
    }
}
