@use 'additional/mixins' as *;

.banner {
  color: var(--clr-black);
  padding: 1.875rem 0;
  background: linear-gradient(308deg, var(--clr-gradient-1) 0, var(--clr-gradient-2) 100%);
  overflow: hidden;

  &__inner {
    display: flex;
    gap: 5rem;
    width: fit-content;
    animation: scroll 15s linear infinite;
  }

  &__text {
    display: flex;
    align-items: center;
    gap: 1.5625rem;
    font-size: clamp(1rem, 2vw + 0.1rem, 2.25rem);
    font-weight: 700;
    letter-spacing: 1.44px;
    text-transform: uppercase;
    white-space: nowrap;

    &::before {
      content: '';
      width: 3.4375rem;
      height: 3.4375rem;
      background-image: url('../img/svg/icons/discount.svg');
      background-repeat: no-repeat;
      background-size: cover;
      background-position: center;
    }
  }
}

@media (max-width: 1320px) {
  .banner {
    padding: 1rem 0;

    &__inner {
      animation: unset;
      justify-content: center;
      width: 100%;
    }

    &__text {
      text-align: center;

      &--clone {
        display: none;
      }
    }
  }
}

@include breakpoint-down(xlarge) {
}

@include breakpoint-down(large) {
  .banner {
    &__text {
      gap: 1rem;
      white-space: wrap;
      &::before {
        min-width: 2.5rem;
        min-height: 2.5rem;
      }
    }
  }
}

@include breakpoint-down(medium) {
}

@include breakpoint-down(small) {
}

@include breakpoint-down(xsmall) {
  .banner {
    &__text {
      gap: 0;
      
      &::before {
        display: none;
      }
    }
  }
}