@use 'additional/mixins' as *;

.development {
  &__title {
    margin-bottom: 5.625rem;
  }
  &__inner {
    display: flex;
    justify-content: space-between;
    gap: 1.25rem;
  }
  &__list {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    gap: 5.3125rem;

    &--center {
      justify-content: space-around;
      border-left: 1px solid rgba(255, 255, 255, 0.1);
      border-right: 1px solid rgba(255, 255, 255, 0.1);
      padding: 0 1.875rem;
    }
  }
  &__item {
    display: flex;
    align-items: center;
    gap: 1.875rem;

    &--center {
      svg {
        transform: scaleX(-1);
      }
    }

    svg {
      width: 70px;
      height: 70px;
    }
  }
  &__photo {
    max-width: 10rem;
    width: 160px;
    height: 160px;
  }

  &__heading {
    margin-bottom: 0.625rem;
    font-size: 1.5rem;
  }
  &__text {
    line-height: 2;
    opacity: 0.5;
    max-width: 20rem;
  }
}

@include breakpoint-down(large) {
  .development {
    &__inner {
      flex-direction: column;
    }

    &__title {
      margin-bottom: 2rem;
    }

    &__list {
      flex-direction: row;
      gap: 1.5rem;
    }

    &__item {
      flex-direction: column;

      svg {
        rotate: 90deg
      }
    }

    &__heading {
      text-align: center;
    }
  }
}

@include breakpoint-down(small) {
  .development {
    &__list {
      flex-direction: column;

      &--center {
        display: none;
      }
    }

    &__text {
      text-align: center;
    }
  }
}