@use 'sass:color';
@use 'mixins' as *;
@use 'colors' as *;
@use 'font-styles' as *;
@use 'z-index' as *;

:root {
  --ff-1: 'PlusJakartaSans', sans-serif;
  --container: 80rem;

  @each $name, $value in $colors {
    --clr-#{$name}: #{$value};
    // --clr-#{$name}-0: #{$value};
    // --clr-#{$name}-1: #{color.adjust($value, $lightness: 15%)};
    // --clr-#{$name}-2: #{color.adjust($value, $lightness: 30%)};
    // --clr-#{$name}-3: #{color.adjust($value, $lightness: 45%)};
    // --clr-#{$name}-4: #{color.adjust($value, $lightness: 60%)};
  }

  @each $screen-size in $breakpoints-down {
    @if $screen-size == xxlarge {
    } @else if $screen-size == xxlarge {
      @include breakpoint-down(xxlarge) {
        --container: 87.4988rem;
      }
    } @else if $screen-size == xlarge {
      @include breakpoint-down(xlarge) {
        --container: 74.9988rem;
      }
    } @else if $screen-size == large {
      @include breakpoint-down(large) {
        --container: 61.9988rem;
      }
    } @else if $screen-size == medium {
      @include breakpoint-down(medium) {
        --container: 47.9988rem;
      }
    } @else if $screen-size == small {
      @include breakpoint-down(small) {
        --container: 35.9988rem;
      }
    } @else if $screen-size == xsmall {
      @include breakpoint-down(xsmall) {
        --container: 30.0613rem;
      }
    } @else if $screen-size == xxsmall {
      @include breakpoint-down(xxsmall) {
        --container: 100%;
      }
    }
  }

  // @each $value, $weight in $font-weights {
  //   --fw-#{$weight}: #{$value};
  // }

  // @each $index, $height in $z-index {
  //   --i-#{$index}: #{$height};
  // }
}
