@use 'additional/mixins' as *;

.hero {
  padding-bottom: 12.5rem;
  position: relative;

  &::before {
    content: '';
    position: absolute;
    left: 0;
    bottom: 50px;
    width: 56.25rem;
    height: 18.125rem;
    @include adaptivValue('left', 0, -350, 1280, 600);
    @include adaptivValue('width', 900, 650, 1280, 600);
    background-image: url('../img/hero-img.png');
    background-repeat: no-repeat;
    background-size: contain;
  }

  &::after {
    content: '';
    position: absolute;
    right: 430px;
    top: 100px;
    width: 23.125rem;
    height: 23.125rem;
    @include adaptivValue('right', 430, 25, 1280, 100);
    background-image: url('../img/substances/substance-2.png');
    background-repeat: no-repeat;
    background-size: contain;
    animation: upToDown 2s ease-in-out alternate-reverse infinite;
  }
  &__title {
    background: linear-gradient(to right, #fff 10%, rgba(#fff, 0.3), #fff 75%);
    margin-bottom: 4.0625rem;
    @include gradientText;

    span {
      background: linear-gradient(to right, var(--clr-gradient-1), var(--clr-gradient-2));
      @include gradientText;
    }
  }
  &__inner {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
  }
  &__wrapper {
    display: flex;
    justify-content: space-between;
    gap: 20px;
    width: 100%;
    margin-bottom: 3.125rem;
  }
  &__cards {
    display: flex;
    gap: 1.875rem;
  }
  &__card {
    border-radius: 2rem;
    padding: 1.875rem;
    width: fit-content;
    display: flex;
    flex-direction: column;
    overflow: hidden;
    position: relative;

    &:last-of-type {
      padding: 1.875rem 0 1.875rem 1.875rem;
    }

    &::before {
      --_mask-dir: to right;
      content: '';
      position: absolute;
      inset: 0;
      border-radius: 2rem;

      border: 1px solid rgba(255, 255, 255, 0.3);
      background-color: rgba(210, 210, 210, 0.1);
      -webkit-mask-image: linear-gradient(var(--_mask-dir), transparent 5%, rgba(33, 33, 33, 0.7));
    }

    &--reverse {
      &::before {
        --_mask-dir: to left;
      }
    }
  }
  &__num {
    font-size: 2.5rem;
    letter-spacing: 1.5px;
    line-height: 1.5;
    min-width: 10rem;
  }
  &__text {
    opacity: 0.5;
    font-weight: 500;
    line-height: 2;
    text-transform: uppercase;
  }
  &__description {
    max-width: 23.4375rem;
    letter-spacing: -0.6px;
    opacity: 0.5;
  }
  &__link {
    width: fit-content;
    text-transform: uppercase;
    font-weight: 600;
    max-width: 12.5rem;
    width: 100%;
    padding: 1.5625rem 0.9375rem;
    color: var(--clr-black);
    background-color: var(--clr-white);
    text-align: center;
    border-radius: 100vmax;

    &:first-of-type {
      margin-right: 12.5rem;
      margin-bottom: 2.1875rem;
    }

    &:last-of-type {
      background: none;
      color: var(--clr-white);

      @media (hover: hover) {
        &:hover {
          color: var(--clr-black);
        }
      }

      &::after {
        content: '';
        position: absolute;
        inset: 0;
        border-radius: 100vmax;
        border: 1px solid rgba(255, 255, 255, 0.5);
        -webkit-mask-image: linear-gradient(to right, transparent 10%, rgba(33, 33, 33, 0.7));
      }
    }

    // &:hover::after {
    //   border-color: none !important;
    // }
  }
}

@include breakpoint-down(xlarge) {
  .hero {
    &::after {
      opacity: 0.4;
      filter: blur(10px);
    }
  }
}

@include breakpoint-down(medium) {
  .hero {
    &__title {
      text-align: center;
      margin-bottom: 2rem;
    }

    &__cards {
      justify-content: center;
    }

    &__description {
      text-align: center;
      margin-inline: auto;
    }

    &__wrapper {
      margin-bottom: 1.5rem;
    }

    &__link {
      &:first-of-type {
        margin-right: unset;
      }
      margin-inline: auto !important;
    }

    &::before,
    &::after {
      opacity: 0.2;
      filter: blur(2px);
    }
    &__wrapper {
      flex-direction: column;
    }
  }
}

@include breakpoint-down(small) {
  .hero {
    &__cards {
      display: inline-block;
      margin-inline: auto;
    }
  }
}
