@use 'additional/mixins' as *;

.header {
  position: sticky;
  top: 0;
  z-index: 999;
  padding: 1.5625rem 0;
  transition: 0.2s;

  &.sticky {
    background-color: rgba(255, 255, 255, 0.01);
    backdrop-filter: blur(13px);
    color: var(--clr-white);
    padding: 0;

    .nav__link {
      opacity: 1;
      padding: 1.3rem;
    }
  }

  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 1.875rem;
  }

  &__logo {
    font-family: 'Raleway', sans-serif;
    font-size: 1.5rem;
    font-weight: 700;

    span {
      background: linear-gradient(to right, var(--clr-gradient-1), var(--clr-gradient-2));
      @include gradientText;
    }
  }

  &__link {
    display: flex;
    align-items: center;
    text-transform: uppercase;
    gap: 0.625rem;
    text-align: center;
    font-weight: 500;

    @include gradientText;
    background-image: linear-gradient(
      to right,
      var(--clr-gradient-1),
      var(--clr-gradient-2) 25% 50%,
      transparent 50% 0
    );
    background-color: var(--clr-white);
    background-size: 200%;
    background-position: right;
    transition: 0.3s;

    @media (hover: hover) {
      &:hover {
        background-position: left;

        svg {
          translate: 5px -5px;
        }
      }
    }

    svg {
      stroke: var(--clr-white);
      width: 15px;
      height: 15px;
      transition: 0.2s;
    }
  }
}

.nav {
  transition: 0.2s;
  &:has(.nav__list.active) {
    width: 100%;
  }
  &__list {
    display: grid;
    grid-auto-columns: 1fr;
    grid-auto-flow: column;
    transition: 0.2s;

    &.active {
      right: 0;
    }
  }

  &__link {
    padding: 0.625rem 1.5625rem;
    opacity: 0.5;
    transition: 0.2s;
    text-align: center;

    @media (hover: hover) {
      &:hover {
        background-color: var(--clr-white);
        color: var(--clr-black);
        opacity: 1;
      }
    }
  }
}

@include breakpoint-down(large) {
  .header {
    &__inner {
      gap: 1rem;
    }

    &__link {
      margin-left: auto;
    }
    &.sticky {
      padding: 0.625rem 0;
    }
  }
  .nav {
    overflow: hidden;
    position: absolute;
    top: 0;
    right: 0;
    z-index: -1;
    width: 0;
    height: 100dvh;
    max-height: 30rem;

    &__list {
      position: absolute;
      top: 0;
      right: -100%;
      width: 100%;
      background-color: var(--clr-white);
      color: var(--clr-black);
      padding: 4.375rem 0;
      grid-auto-flow: row;
    }

    &__link {
      padding: 1rem;
      opacity: 1;
    }
  }
}

@include breakpoint-down(xsmall) {
  .header {
    &__link {
      margin: 0;
    }
  }
}
