@use 'additional/mixins' as *;

.burger {
  display: none;
  position: relative;
  z-index: 10;
}
#burger-checkbox {
  display: none;
}

.toggle {
  position: relative;
  width: 2.5rem;
  height: 2.5rem;
  cursor: pointer;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 10px;
  transition-duration: 0.5s;
}

.bars {
  width: 100%;
  height: 3px;
  background-color: var(--clr-white);
  border-radius: 4px;
}

#bar2 {
  transition-duration: 0.8s;
}

#bar1,
#bar3 {
  width: 70%;
}

#burger-checkbox:checked + .toggle .bars {
  position: absolute;
  transition-duration: 0.5s;
  background-color: var(--clr-black);
}

#burger-checkbox:checked + .toggle {
  transition-duration: 0.5s;
  transform: rotate(180deg);

  #bar2 {
    transform: scaleX(0);
    transition-duration: 0.5s;
  }

  #bar1 {
    width: 100%;
    transform: rotate(45deg);
    transition-duration: 0.5s;
  }

  #bar3 {
    width: 100%;
    transform: rotate(-45deg);
    transition-duration: 0.5s;
  }
}

@include breakpoint-down(large) {
  .burger {
    display: block;
  }
}
