:root {
  --ff-1: "PlusJakartaSans", sans-serif;
  --container: 80rem;
  --clr-black: black;
  --clr-white: white;
  --clr-bg-main: #1A1A1A;
  --clr-gradient-1: #a7f306;
  --clr-gradient-2: #f3cd06;
}

@keyframes scroll {
  0% {
    transform: translateX(0%);
  }

  100% {
    transform: translateX(-50%);
  }
}

@keyframes upToDown {
  0% {
    transform: translateY(10px);
  }

  100% {
    transform: translateY(50px);
  }
}

@keyframes changeFilter {
  0% {
    filter: grayscale(100%);
  }

  100% {
    filter: grayscale(0);
  }
}

body {
  font-family: var(--ff-1);
  font-size: clamp(1rem, 1vw + 0.1rem, 1.25rem);
  font-weight: 400;
  line-height: 1.5;
  color: var(--clr-white);
  background-color: var(--clr-bg-main);
  min-height: 100%;
  display: grid;
  grid-template-rows: 1fr min-content;
  align-content: start;
}

.container {
  max-width: var(--container);
  width: min(100% - 2rem, 80rem);
  margin-inline: auto;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Raleway", sans-serif;
}

h1 {
  font-size: clamp(2rem, 5.9vw + 0.1rem, 7.5rem);
  font-weight: 800;
}

h2 {
  font-size: clamp(1.6rem, 5vw + 0.1rem, 5rem);
  font-weight: 800;
  letter-spacing: 0.04em;
}

p {
  line-height: 2;
}

a {
  display: block;
}

section {
  padding: var(--section-pd) 0;
}

section:nth-child(1) {
  --section-pd: 100px;
}

section:nth-child(2) {
  --section-pd: 100px;
}

section:nth-child(3) {
  --section-pd: 100px;
}

section:nth-child(4) {
  --section-pd: 100px;
}

section:nth-child(5) {
