.burger {
  display: none;
  position: relative;
  z-index: 10;
}

#burger-checkbox {
  display: none;
}

.toggle {
  position: relative;
  width: 2.5rem;
  height: 2.5rem;
  cursor: pointer;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 10px;
  transition-duration: 0.5s;
}

.bars {
  width: 100%;
  height: 3px;
  background-color: var(--clr-white);
  border-radius: 4px;
}

#bar2 {
  transition-duration: 0.8s;
}

#bar1,
#bar3 {
  width: 70%;
}

#burger-checkbox:checked + .toggle .bars {
  position: absolute;
  transition-duration: 0.5s;
  background-color: var(--clr-black);
}

#burger-checkbox:checked + .toggle {
  transition-duration: 0.5s;
  transform: rotate(180deg);
}

#burger-checkbox:checked + .toggle #bar2 {
  transform: scaleX(0);
  transition-duration: 0.5s;
}

#burger-checkbox:checked + .toggle #bar1 {
  width: 100%;
  transform: rotate(45deg);
  transition-duration: 0.5s;
}

#burger-checkbox:checked + .toggle #bar3 {
  width: 100%;
  transform: rotate(-45deg);
  transition-duration: 0.5s;
}

.btn {
  max-width: 12.5rem;
  width: 100%;
  font-size: 1.25rem;
  font-weight: 600;
  cursor: pointer;
  text-align: center;
  text-transform: uppercase;
  border: 1px solid transparent;
  color: var(--clr-white);
  transition: 0.1s;
  z-index: 1;
  overflow: hidden;
  position: relative;
  transition: 0.3s;
}

.btn::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  width: 0;
  height: 300%;
  transform: translate(-50%, -50%) rotate(45deg);
  background: linear-gradient(var(--clr-gradient-1) 0, var(--clr-gradient-2) 100%);
  transition: 0.4s ease;
  z-index: -1;
}

.btn:active {
  transform: scale(0.95);
}

.header {
  position: sticky;
  top: 0;
  z-index: 999;
  padding: 1.5625rem 0;
  transition: 0.2s;
}

.header.sticky {
  background-color: rgba(255, 255, 255, 0.01);
  backdrop-filter: blur(13px);
  color: var(--clr-white);
  padding: 0;
}

.header.sticky .nav__link {
  opacity: 1;
  padding: 1.3rem;
}

.header__inner {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 1.875rem;
}

.header__logo {
  font-family: "Raleway", sans-serif;
  font-size: 1.5rem;
  font-weight: 700;
}

.header__logo span {
  background: linear-gradient(to right, var(--clr-gradient-1), var(--clr-gradient-2));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.header__link {
  display: flex;
  align-items: center;
  text-transform: uppercase;
  gap: 0.625rem;
  text-align: center;
  font-weight: 500;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-image: linear-gradient(to right, var(--clr-gradient-1), var(--clr-gradient-2) 25% 50%, transparent 50% 0);
  background-color: var(--clr-white);
  background-size: 200%;
  background-position: right;
  transition: 0.3s;
}

.header__link svg {
  stroke: var(--clr-white);
  width: 15px;
  height: 15px;
  transition: 0.2s;
}

.nav {
  transition: 0.2s;
}

.nav:has(.nav__list.active) {
  width: 100%;
}

.nav__list {
  display: grid;
  grid-auto-columns: 1fr;
  grid-auto-flow: column;
  transition: 0.2s;
}

.nav__list.active {
  right: 0;
}

.nav__link {
  padding: 0.625rem 1.5625rem;
  opacity: 0.5;
  transition: 0.2s;
  text-align: center;
}

.hero {
  padding-bottom: 12.5rem;
  position: relative;
}

.hero::before {
  content: "";
  position: absolute;
  left: 0;
  bottom: 50px;
  width: 56.25rem;
  height: 18.125rem;
  left: 0px;
  width: 900px;
  background-image: url("../img/hero-img.png");
  background-repeat: no-repeat;
  background-size: contain;
}

.hero::after {
  content: "";
  position: absolute;
  right: 430px;
  top: 100px;
  width: 23.125rem;
  height: 23.125rem;
  right: 430px;
  background-image: url("../img/substances/substance-2.png");
  background-repeat: no-repeat;
  background-size: contain;
  animation: upToDown 2s ease-in-out alternate-reverse infinite;
}

.hero__title {
  background: linear-gradient(to right, #fff 10%, rgba(255, 255, 255, 0.3), #fff 75%);
  margin-bottom: 4.0625rem;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.hero__title span {
  background: linear-gradient(to right, var(--clr-gradient-1), var(--clr-gradient-2));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.hero__inner {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}

.hero__wrapper {
  display: flex;
  justify-content: space-between;
  gap: 20px;
  width: 100%;
  margin-bottom: 3.125rem;
}

.hero__cards {
  display: flex;
  gap: 1.875rem;
}

.hero__card {
  border-radius: 2rem;
  padding: 1.875rem;
  width: fit-content;
  display: flex;
