@use 'additional/mixins' as *;

.btn {
  max-width: 12.5rem;
  width: 100%;
  font-size: 1.25rem;
  font-weight: 600;
  cursor: pointer;
  text-align: center;
  text-transform: uppercase;
  border: 1px solid transparent;
  color: var(--clr-white);
  transition: 0.1s;
  z-index: 1;
  overflow: hidden;
  position: relative;
  transition: 0.3s;

  &::before {
    content: '';
    position: absolute;
    top: 50%;
    left: 50%;
    width: 0;
    height: 300%;
    transform: translate(-50%, -50%) rotate(45deg);
    background: linear-gradient(var(--clr-gradient-1) 0, var(--clr-gradient-2) 100%);
    transition: 0.4s ease;
    z-index: -1;
  }

  @media (hover: hover) {
    &:hover {
      color: var(--clr-black);
      border-color: transparent;

      &::before {
        width: 105%;
      }
    }
  }

  &:active {
    transform: scale(0.95);
  }
}

@include breakpoint-down(xsmall) {
  .btn {
    max-width: 100% !important;
  }
}
