@use 'additional/mixins' as *;

body {
  font-family: var(--ff-1);
  font-size:  clamp(1rem, 1vw + 0.1rem, 1.25rem);
  font-weight: 400;
  line-height: 1.5;
  color: var(--clr-white);
  background-color: var(--clr-bg-main);
  min-height: 100%;
  display: grid;
  grid-template-rows: 1fr min-content;
  align-content: start;
}

.container {
  max-width: var(--container);
  width: min(calc(100% - 2rem), 80rem);
  margin-inline: auto;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: 'Raleway', sans-serif;
}

h1 {
  font-size: clamp(2rem, 5.9vw + 0.1rem, 7.5rem);
  font-weight: 800;
}

h2 {
  font-size: clamp(1.6rem, 5vw + 0.1rem, 5rem);
  font-weight: 800;
  letter-spacing: 0.04em;
}

p {
  line-height: 2;
}

a {
  display: block;
}

section {
  // --section-pd: 100px;
  padding: var(--section-pd) 0;
}


@for $i from 1 through 6 {
  section:nth-child(#{$i}) {
    @include adaptivValue('--section-pd', 100, 30, 1280, 375);
  }
}
