@use 'additional/mixins' as *;

.partners {
  &__title {
    text-align: center;
    margin-bottom: 5.625rem;
  }
  &__inner {
    overflow: hidden;
    display: flex;
    flex-direction: column;
    gap: 5rem;
  }
  &__list {
    width: fit-content;
    display: flex;
    gap: 3.125rem;
    animation: scroll 25s linear reverse infinite;

    &:last-of-type {
      animation: scroll 25s linear infinite;
    }

    &--reverse {
      .partners__item {
        &::before {
          --_mask-dir: to bottom;
        }
      }
    }
  }

  &__item {
    padding: 2.1875rem 2.5rem;
    background: linear-gradient(180deg, rgba(255, 255, 255, 0.2) 0%, rgba(255, 255, 255, 0) 100%);
    border-radius: 2rem;
    width: 15rem;

    overflow: hidden;
    position: relative;

    &::before {
      --_mask-dir: to top;
      content: '';
      position: absolute;
      inset: 0;
      border-radius: 2rem;

      border: 1px solid rgba(255, 255, 255, 0.3);
      background-color: rgba(210, 210, 210, 0.1);
      -webkit-mask-image: linear-gradient(
        var(--_mask-dir),
        transparent 5%,
        rgba(255, 255, 255, 0.1)
      );
    }
  }
}

@include breakpoint-down(xxlarge) {
  .partners {
    &__inner {
      gap: 2.5rem;
    }
    
    &__list {
      gap: 1.5rem;
    }

    &__item {
      padding: 1.3rem;
    }

    &__logo {
      width: 75%;
      margin-inline: auto;
    }
  }
}

@media (max-width: 1320px) {
  .partners {
    &__list {
      animation: unset;
      flex-wrap: wrap;
      justify-content: center;
      &:last-of-type {
        display: none;
      }
    }

    &__item {
      &--clone {
        display: none;
      }
    }
  }
}

@include breakpoint-down(large) {

  .partners {
    &__title {
      margin-bottom: 1.5rem;
    }

    &__list {


    }

    &__item {
      padding: 1rem;
    }

    &__logo {
      width: 60%;
    }
  }
}
