@use 'additional/mixins' as *;
.technologies {
  &__header {
    margin-bottom: 5.625rem;
    display: flex;
    justify-content: space-between;
  }

  &__icon {
    width: 60px;
    height: 60px;
    stroke: var(--clr-white);
  }
  &__points {
    display: flex;
    justify-content: space-between;
    margin-bottom: 10rem;
    background: linear-gradient(308deg, var(--clr-gradient-1) 0, var(--clr-gradient-2) 100%);
  }
  &__point {
    border-bottom: 1px solid transparent;
    border-bottom-color: #ccc;
    padding: 1.5625rem;
    text-transform: uppercase;
    font-size: clamp(1.3rem, 3vw + 0.1rem, 2rem);
    flex: 1;
    font-weight: 600;
    text-align: center;
    cursor: pointer;
    transition: 0.1s;
    background: var(--clr-bg-main);

    &.active {
      border-bottom: 1px solid;
      border-image-slice: 1;
      border-image-source: linear-gradient(
        308deg,
        var(--clr-gradient-1) 0,
        var(--clr-gradient-2) 100%
      );
    }

    @media (hover: hover) {
      &:hover {
        background-color: transparent;
        color: var(--clr-black);
      }
    }
  }

  &__content {
    display: none;
    justify-content: center;
    align-items: flex-start;
    gap: 20px;

    &.active {
      display: flex;
    }
  }
  &__heading {
    margin-bottom: 1.875rem;
    text-transform: uppercase;
    background: linear-gradient(to right, #fff 10%, rgba(#fff, 0.3), #fff 75%);
    @include gradientText;

    span {
      background: linear-gradient(to right, var(--clr-gradient-1), var(--clr-gradient-2));
      @include gradientText;
    }
  }
  &__text {
    margin-bottom: 2.1875rem;
    opacity: 0.5;
    line-height: 2;
  }
  &__list {
    display: flex;
    flex-direction: column;
    gap: 1.75rem;
    margin-bottom: 2.8125rem;
  }
  &__item {
    text-transform: capitalize;
    display: flex;
    align-items: center;
    gap: 0.9375rem;

    &::before {
      content: url('../img/svg/icons/tick.svg');
      width: 2rem;
      height: 2rem;
      display: flex;
      justify-content: center;
      align-items: center;
      border-radius: 100vmax;
      background-color: rgba(255, 255, 255, 0.08);
    }
  }
  &__link {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 1.5625rem;
    padding: 1.5625rem;
    text-transform: uppercase;
    border-radius: 100vmax;
    border: 1px solid rgba(255, 255, 255, 0.2);
    max-width: 15.625rem;

    svg {
      width: 18px;
      height: 18px;
      stroke: var(--clr-white);
      transition: 0.2s;
    }

    &:hover {
      svg {
        stroke: var(--clr-black);
      }
    }
  }

  &__img {
    max-width: 35.625rem;
    animation: changeFilter 4s infinite alternate;
    @include adaptivValue('max-width', 570, 350, 1280, 768);
  }
}

@include breakpoint-down(large) {
  .technologies {
    &__points {
      flex-wrap: wrap;
      margin-bottom: 3rem;
    }

    &__content {
      position: relative;
    }
    &__text {
      max-width: 31.25rem;
    }
  }
}

@include breakpoint-down(medium) {
  .technologies {
    &__points {
      flex-wrap: wrap;
    }

    &__header {
      margin-bottom: 2rem;
    }

    &__icon {
      width: 40px;
      height: 40px;
    }

    &__img {
      position: absolute;
      right: 0;
      bottom: 0;
      opacity: 0.2;
    }
  }
}

@include breakpoint-down(xsmall) {
  .technologies {
    &__points {
      border: 1px solid rgba(255, 255, 255, 0.2);
    }

    &__point {
      border: 1px solid rgba(255, 255, 255, 0.2);
    }
    &__icon {
      width: 25px;
      height: 25px;
    }
  }
}
