@use 'additional/mixins' as *;

.footer {
  @include adaptivValue('padding-block', 100, 50, 1280, 480);

  &__inner {
    display: flex;
    gap: 1.875rem;
    justify-content: space-between;
    width: 100%;
  }
  &__wrapper {
    width: 100%;
  }
  &__logo {
    font-size: 1.5rem;
    margin-bottom: 1.875rem;
    font-weight: 700;
    font-family: 'Raleway', sans-serif;

    span {
      background: linear-gradient(to right, var(--clr-gradient-1), var(--clr-gradient-2));
      @include gradientText;
    }
  }

  &__text {
    max-width: 20.625rem;
    line-height: 2;
    opacity: 0.5;
  }
  &__list {
    display: flex;
    padding-bottom: 3.125rem;
    border-bottom: 1px solid rgba(#fff, 0.2);
    margin-bottom: 4.375rem;
  }
  &__item {
    text-transform: uppercase;

    a {
      padding: 0.625rem 1.5625rem;
      transition: 0.2s ease;
      width: fit-content;
      position: relative;

      &::before {
        content: '';
        position: absolute;
        left: 0;
        bottom: 0;
        height: 2px;
        width: 0;
        background-color: var(--clr-gradient-2);
        transition: 0.2s ease;
      }

      &::after {
        content: '';
        position: absolute;
        left: 0;
        bottom: 0;
        height: 0;
        width: 100%;
        background-color: var(--clr-gradient-2);
        transition: 0.2s ease;
        z-index: -1;
      }

      @media (hover: hover) {
        &:hover {
          color: var(--clr-black);
          transition-delay: 0.3s;

          &::before {
            width: 100%;
          }

          &::after {
            color: var(--clr-white);
            height: 100%;
            transition-delay: 0.2s;
          }
        }
      }
    }
  }
  &__block {
    display: flex;
  }
  &__link {
    text-transform: uppercase;
    opacity: 0.5;
    font-size: 1rem;

    &:first-of-type {
      margin-left: auto;
      margin-right: 3.75rem;
    }

    &:hover {
      text-decoration: underline;
    }
  }
}

.copyright {
  text-transform: uppercase;
  opacity: 0.5;
  font-size: 1rem;
}

@include breakpoint-down(xlarge) {
  .footer {
    &__inner {
      flex-direction: column;
    }

    &__list {
      justify-content: center;
      padding-bottom: 1.25rem;
      margin-bottom: 1.875rem;
    }

    &__block {
      justify-content: space-between;
    }

    &__link {
      &:first-of-type {
        margin: 0;
      }
    }
  }
}

@include breakpoint-down(medium) {
  .footer {
    &__block {
      flex-wrap: wrap;
      gap: 0.9375rem;
    }

    &__list {
      flex-wrap: wrap;
    }
  }
}

@include breakpoint-down(xsmall) {
  .footer {
    &__list {
      flex-direction: column;
      gap: 1.25rem;
    }
    &__item {
      a {
        padding: 0;
      }
    }
  }
}
